import React, {useEffect} from 'react';

import {makeStyles} from '@material-ui/core/styles';
import Button from "@material-ui/core/Button";

const useStyles=makeStyles((theme)=>({
  root:{
    display:'flex',
    flexDirection:'row',
    justifyContent:'flex-end',
    margin:theme.spacing(1)
  },
  kakaoBtn: {
    padding: 0,
  },
}))

const KakaoBtn=()=>{
    const classes=useStyles();

    useEffect(()=>{
      if (window.Kakao) {
        const kakao = window.Kakao
        // 중복 initialization 방지
        if (!kakao.isInitialized()) {
          kakao.init(process.env.REACT_APP_KAKAO_JS_KEY)
        }
        kakao.Link.createDefaultButton({
          // Render 부분 id=kakao-link-btn 을 찾아 그부분에 렌더링을 합니다
          container: '#kakao-link-btn',
          objectType: 'feed',
          content: {
            title: '갯마을바지락칼국수보쌈',
            description: '#칼국수 #보쌈 #당수동 #맛집',
            imageUrl: 'images/icons/logo192.png', //추후 s3에서 가져오자
            link: {
              mobileWebUrl: 'https://wslee950920.github.io/gatmauel-user-front/',
              webUrl: 'https://wslee950920.github.io/gatmauel-user-front/',
            },
          },
          buttons: [
            {
              title: '홈페이지 방문하기',
              link: {
                mobileWebUrl: 'https://wslee950920.github.io/gatmauel-user-front/',
                webUrl: 'https://wslee950920.github.io/gatmauel-user-front/',
              },
            },
          ],
        })
      }
    }, []);

    return(
      <div className={classes.root}>
           <Button id="kakao-link-btn" classes={{ text: classes.kakaoBtn }} size='small'>
              <img src="images/icons/kakaolink_btn_small.png" alt="kakao-share-icon" />
          </Button>
      </div>
    )
}

export default KakaoBtn;